.rightbarr {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 40%;
    background-color: white;
    z-index: 1;
    position: fixed;
    right: 0px;
    padding-top: 12px;
    top: 0px;
    height: 100%;
    overflow-x: hidden;
  }
  
.rightbarr li {
    display: block;
    color: #000;
    padding: 20px 16px;
    text-decoration: none;
    border: 2px solid #008CBA;
    border-radius: 12px;
    margin: 20px;
  }
  
.rightbarr li:hover {
    background-color: #44546A;
    color: white;
  }

.btn22 {
    width: 100px;
    z-index: 2;
    position:absolute;
    top:0;
    left:0;
    margin: 20px;
}