body {
    background-image: url("../../imgs/queue1.png");
    background-repeat: no-repeat;
    background-position: 580px -50px;
    background-size: 800 449;
  }

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 60px 60px 100px 100px 60px 60px 60px 60px;
    grid-gap: 10px;
    padding: 10px;
  }
  
  .grid-container > div {
    text-align: center;
  }
  
  .loginB {
    grid-area: 1 / 1 / 2 / 2;
  }

  .title {
    grid-area: 3 / 2 / 6 / 5;
  }

  .browseB {
    grid-area: 7 / 3 / 8 / 4;
  }

  #title1 {
    font-size: 70px;
    font-weight: 400;
  }

  #subtitle1 {
    font-size: 50px;
    font-weight: 300;
  }

  .Btn {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    background-color: white; 
    color: black; 
    border: 2px solid #008CBA;
    border-radius: 12px;
  }
